@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;600;800&display=swap);
.CountryCard{display:grid;justify-content:center;grid-template-columns:repeat(auto-fill, 264px);grid-gap:74px;gap:74px}@media only screen and (max-width: 650px){.CountryCard{justify-items:center;grid-template-columns:auto;grid-gap:40px;gap:40px;padding:0 56px}}.CountryCard .Box{width:264px;height:336px;display:flex;flex-direction:column;box-shadow:0px 0px 7px 2px rgba(0,0,0,0.0294384);border-radius:5px}.CountryCard .Box .image{height:160px;background-size:cover;background-position:center;border-radius:5px 5px 0 0}.CountryCard .Box .content{padding:24px 24px 40px 24px}.CountryCard .Box .content h2{margin-bottom:16px;font-weight:800;font-size:18px;line-height:26px}.CountryCard .Box .content h3{margin-bottom:8px;font-weight:700;font-size:14px;line-height:16px}.CountryCard .Box .content h3 span{font-weight:600}

.CountryContainer{padding:0 80px}@media only screen and (max-width: 650px){.CountryContainer{padding:0 16px}}.CountryContainer nav.CountryNavigation{margin:48px 0;display:flex;justify-content:space-between}@media only screen and (max-width: 650px){.CountryContainer nav.CountryNavigation{flex-direction:column}}.CountryContainer nav.CountryNavigation .SearchBar{margin-right:10px;border-radius:5px;max-width:480px;width:100%;height:56px;display:flex;align-items:center}@media only screen and (max-width: 650px){.CountryContainer nav.CountryNavigation .SearchBar{max-width:100%;margin:0 0 40px 0}}.CountryContainer nav.CountryNavigation .SearchBar svg{margin:0 24px 0 32px}.CountryContainer nav.CountryNavigation .SearchBar input{background:transparent;border:none;font-family:"Nunito", sans-serif;padding:0;width:100%;height:100%;outline:none}

.CountryContainer .BackLink{width:136px;display:flex;margin:80px 0 40px 0}@media only screen and (max-width: 650px){.CountryContainer .BackLink{margin:40px 0 24px 0}}.CountryContainer .BackLink svg{display:block;width:19px;height:19px;margin-right:10.5px}.CountryContainer .Container{display:flex;justify-content:space-between;flex-wrap:wrap;align-items:center}@media only screen and (max-width: 650px){.CountryContainer .Container{justify-content:center}}.CountryContainer .Container .Column{margin-top:40px}.CountryContainer .Container .Column.Img{width:560px;height:401px;background-size:cover;background-repeat:no-repeat;background-position:center}@media only screen and (max-width: 650px){.CountryContainer .Container .Column.Img{width:320px;height:230px}}.CountryContainer .Container .Column.Content{width:598px;margin-left:40px}@media only screen and (max-width: 650px){.CountryContainer .Container .Column.Content{margin:44px 10px 0 10px}}.CountryContainer .Container .Column.Content h2{font-weight:800;font-size:32px;line-height:44px;margin-bottom:23px}@media only screen and (max-width: 650px){.CountryContainer .Container .Column.Content h2{font-size:22px;line-height:30px}}.CountryContainer .Container .Column.Content h4{font-weight:600;margin-bottom:10px}@media only screen and (max-width: 650px){.CountryContainer .Container .Column.Content h4{font-size:14px}}.CountryContainer .Container .Column.Content h4>span{font-weight:300}.CountryContainer .Container .Column.Content .SubCol{display:flex;justify-content:space-between}@media only screen and (max-width: 650px){.CountryContainer .Container .Column.Content .SubCol{flex-direction:column}.CountryContainer .Container .Column.Content .SubCol>div{margin-bottom:32px}}.CountryContainer .Container .Column.Content .Borders{display:flex;margin-top:68px}@media only screen and (max-width: 650px){.CountryContainer .Container .Column.Content .Borders{margin:34px 0 60px 0;flex-direction:column}}.CountryContainer .Container .Column.Content .Borders h4{width:112px;margin-right:20px}.CountryContainer .Container .Column.Content .Borders>div{display:flex;flex-wrap:wrap}.CountryContainer .Container .Column.Content .Borders .Border{display:flex;justify-content:center;align-items:center;margin-bottom:10px;margin-right:10px;height:28px;padding:0 25px;background-color:#fff;box-shadow:0px 0px 4px 1px rgba(0,0,0,0.104931);border-radius:2px;transition:background-color 0.2s ease}.CountryContainer .Container .Column.Content .Borders .Border:hover{background-color:#f2f2f2}

header{height:80px;padding:0 80px;display:flex;justify-content:space-between;align-items:center}@media only screen and (max-width: 650px){header{padding:0 16px}}header .btn-switcher{cursor:pointer;position:relative}header .btn-switcher:before{display:block;margin-right:8px;content:"";width:20px;height:20px;position:absolute}header .btn-switcher.LightTheme:before{background:url(/countries/static/media/moon.d31f3e2f.svg);background-repeat:no-repeat;right:75px;top:2px}header .btn-switcher.DarkTheme:before{background:url(/countries/static/media/sun.0cad6628.svg);background-repeat:no-repeat;right:77px;bottom:-1px;width:25px;height:25px}

html,body,body div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,figure,footer,hgroup,menu,nav,section,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent;font-family:"Nunito Sans", sans-serif}a,a:hover,a:visited,a:link,a:active{text-decoration:none}h1{font-weight:800;font-size:24px;line-height:33px}.App{width:100%;transition:all 0.5s ease;overflow-x:hidden}.App .page-wrapper{max-width:1440px;width:100%;margin:0 auto}.App .page-wrapper .LoadingContainer{position:fixed;width:100%;height:100%;left:0;top:0;background:white;z-index:10;display:flex;justify-content:center;align-items:center}.App .page-wrapper .LoadingContainer .lds-ring{display:inline-block;position:relative;width:80px;height:80px}.App .page-wrapper .LoadingContainer .lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #000;border-radius:50%;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#000 transparent transparent transparent}.App .page-wrapper .LoadingContainer .lds-ring div:nth-child(1){animation-delay:-0.45s}.App .page-wrapper .LoadingContainer .lds-ring div:nth-child(2){animation-delay:-0.3s}.App .page-wrapper .LoadingContainer .lds-ring div:nth-child(3){animation-delay:-0.15s}@keyframes lds-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.App .page-wrapper header .btn-switcher{transition:font-weight 0.5s ease;height:auto}.App .page-wrapper header .btn-switcher:hover{font-weight:600}

.DarkTheme{background-color:#202c36;height:100vh}.DarkTheme span,.DarkTheme a,.DarkTheme h1,.DarkTheme h2,.DarkTheme h3,.DarkTheme h4,.DarkTheme h5,.DarkTheme h6,.DarkTheme p{color:#fff}.DarkTheme header{background:#2b3844}.DarkTheme header span{background:#2b3844}.DarkTheme .CountryContainer nav.CountryNavigation .SearchBar{background:#2b3844;box-shadow:0px 2px 9px rgba(0,0,0,0.0532439)}.DarkTheme .CountryContainer nav.CountryNavigation .SearchBar svg path{fill:#fff}.DarkTheme .CountryContainer nav.CountryNavigation .SearchBar input{color:#fff}.DarkTheme .CountryContainer nav.CountryNavigation .SearchBar input::-webkit-input-placeholder{color:#fff}.DarkTheme .CountryContainer .CountryCard .Box{background:#2b3844}.DarkTheme .CountryContainer .BackLink svg{fill:#fff}.DarkTheme .CountryContainer .Container .Column.Content .Borders .Border{background:#2c3844}

.LightTheme{background-color:#f2f2f2;height:100vh}.LightTheme span,.LightTheme a,.LightTheme h1,.LightTheme h2,.LightTheme h3,.LightTheme h4,.LightTheme h5,.LightTheme h6,.LightTheme p{color:#111517}.LightTheme header{background:#fff}.LightTheme header span{background:#fff}.LightTheme .CountryContainer nav.CountryNavigation .SearchBar{background:#ffffff;box-shadow:0px 2px 9px rgba(0,0,0,0.0532439)}

